* {
  font-family:  Helvetica, Arial, sans-serif;
  font-size: 1.5rem;
}

body {
  padding: 60px;
}

.title {
  color:palevioletred;
  font-family: 'Open Sans', sans-serif;
  font-size: 2rem;
  text-align: center;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  padding: 10px;
  overflow: hidden;
  width: 300px;

}

.card p {
  padding: 5px;
  font-size: 1rem;
  text-align: center;
}

.card img {
  width: 100%;
  max-height: 170px;
}